.accessgranted{
    background-color: #000A0D;
    box-sizing: border-box;
    width: 60rem;
    border: 1px solid #597b83;
    border-style: groove;
    color: #92AAB0;
    padding: 2rem;
    box-shadow: 0px 0px 20px #597b83;
}

.accessgranted-1{
    height: 25rem;
    padding: 2rem;
    display: flex;
    justify-content: flex-start;
}

.accessgranted-2{
    display: flex;
    flex-direction: column;
}

.profile{
    width: 30%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.content{
    width: 70%;
    text-align: justify;
    margin-block: auto;
    font-size: 1.5rem;
}

table tr{
    text-align: left;
}