.aboutme{
    width: 80%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Geo', sans-serif;
}

.passInput form input{
    outline: none;
    border: none;
    text-align: center;
    background-color: #465456;
    margin-bottom: 1.2rem;
    width: 11rem;
    height: 2rem;
    font-size: 2rem;
    color: green;
    font-family: 'Geo', sans-serif;
    padding: 1rem;
}

.passInput{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 5%;
}

.passInput .denied{
    color: red;
    user-select: none;
}

.passInput .granted{
    color: green;
    font-size: 5rem;
    user-select: none;
}

.displayNone{
    display: none;
}