.list{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: auto;
    z-index: 2;
}

.list a{
    margin-block : 0.2rem;
}

.listitem{
    background-repeat: no-repeat;
    width: 442px;
    height: 64px;
    cursor: pointer;
}

.listitem:hover{
    opacity: 0.9;
}

.list .aboutme{
    background-image: url('./kaset.png');
}

.list .portfolio{
    background-image: url('./portfolio-export.png');
}

.list .timeline{
    background-image: url('./timeline.png');
}

